<template>
    <div class="base">
        <h1>消费金融管理平台</h1>
        <div class="main_center">
            <Form ref="formInline" :model="formInline" :rules="ruleInline">
                <FormItem prop="loginName">
                    <Input type="text" v-model="formInline.loginName" placeholder="用户名" :autofocus="true" size="large">
                        <Icon type="ios-person-outline" slot="prepend"></Icon>
                    </Input>
                </FormItem>
                <FormItem prop="password">
                    <Input type="password" v-model="formInline.password" placeholder="密码" size="large">
                        <Icon type="ios-locked-outline" slot="prepend"></Icon>
                    </Input>
                </FormItem>
                <FormItem>
                    <Button type="primary" @click="handleSubmit('formInline')" long>登录</Button>
                </FormItem>
            </Form>
        </div>
    </div>
	
</template>
<style scoped>
  @import "login.css";
</style>
<script type="text/babel">
  import Cookies from 'js-cookie';
  export default{
  	data(){
        return {
            formInline: {
                loginName: '',
                password: ''
            },
            ruleInline: {
                loginName: [
                    { required: true, message: '请输入用户名', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' }
                ]
            },
            userData:{}
        }
    },
    created: function () {
	}, 
	methods: {
	// whenever the document is resized, re-set the 'fullHeight' variable
		handleSubmit(name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    Cookies.set('user', this.formInline.loginName);
                    Cookies.set('password', this.formInline.password);
                    this.login()
                } else {
                    this.$Message.error('Fail!');
                }
            })
        },
        login(){
            this.handleSpinCustom();
        	let param = {
        		loginName:this.formInline.loginName,
        		password:this.formInline.password
        	}
        	this.$api.get('/user/back/login?loginName='+this.formInline.loginName+'&password='+this.formInline.password,null, r => {
		        console.log(r)
		        if(r.status == 'SUCCESS'){
                //   this.getMenu(r.data.token);
		          this.userData = r.data;
                  this.$store.commit('SET_TOKEN',r.data.token);
                  this.$store.commit('SET_USERDATA',r.data);
                  localStorage.setItem('token',r.data.token)
                  localStorage.setItem('userData',JSON.stringify(r.data));
                  this.$Spin.hide();
                  
                  this.$router.push({
                        name: 'home_index'
                    });
		        }
		    })
        },
        handleSpinCustom () {
            this.$Spin.show({
                render: (h) => {
                    return h('div', [
                        h('Icon', {
                            'class': 'demo-spin-icon-load',
                            props: {
                                type: 'load-c',
                                size: 18
                            }
                        }),
                        h('div', 'Loading')
                    ])
                }
            });
        },
        getMenu(token){
            this.$api.get('/user/back/queryMenu/'+token, {}, r => {
                console.log(r)
                if(r.status == 'SUCCESS'){
                    let json = JSON.parse(r.data);
                    //  console.log('SET_MENULIST');
                    // console.log(json)
                    for(let i=0;i<json.length;i++){
                        let item = json[i];
                        if(item.access==0){
                            this.editmenu(item.name,'',item.access);
                        }
                        if(item.children){
                            let children = item.children;
                            for(let j=0;j<children;j++){
                                let child = children[j];
                                if(child.access==0){
                                    this.editmenu(item.name,child.name,child.access);
                                }
                            }
                        }
                    }
                    // console.log(this.$store.state.menuList)
                }
            })
        },
        editmenu(parentName,childrenName,access){
            console.log('parentName:'+parentName+'-'+'childrenName:'+childrenName+'-'+'access:'+access);
            let menulist = this.$store.state.menuList;
            for(let i=0;i<menulist.length;i++){
                let item = menulist[i];
                if(parentName && !childrenName && parentName==item.title){
                    item.access = access;
                }else if(parentName && childrenName && item.children){
                    let children = item.children;
                    for(let j=0;j<children.length;j++){
                        let child = children[j];
                        if(child.title == childrenName){
                            child.access = access;
                        }
                    }
                }
            }
            console.log(menulist)
            this.$store.commit('SET_MENULIST', menulist);
        }
	}
  }
</script>


